n    left: auto\n    right: 0\n\n.v-progress-linear__background\n  bottom: 0\n  left: 0\n  position: absolute\n  top: 0\n  transition: inherit\n\n.v-progress-linear--reverse\n  .v-progress-linear__background\n    left: auto\n    right: 0\n\n.v-progress-linear__content\n  align-items: center\n  display: flex\n  height: 100%\n  left: 0\n  justify-content: center\n  position: absolute\n  top: 0\n  width: 100%\n\n.v-progress-linear--reverse\n  .v-progress-linear__content\n    left: auto\n    right: 0\n\n.v-progress-linear__determinate\n  height: inherit\n  left: 0\n  position: absolute\n  transition: inherit\n\n.v-progress-linear--reverse\n  .v-progress-linear__determinate\n    left: auto\n    right: 0\n\n.v-progress-linear\n  .v-progress-linear__indeterminate\n    .long, .short\n      background-color: inherit\n      bottom: 0\n      height: inherit\n      left: 0\n      position: absolute\n      right: auto\n      top: 0\n      width: auto\n      will-change: left, right\n\n    &--active .long\n      animation-name: indeterminate-ltr\n      animation-duration: 2.2s\n      animation-iteration-count: infinite\n\n    &--active .short\n      animation-name: indeterminate-short-ltr\n      animation-duration: 2.2s\n      animation-iteration-count: infinite\n\n.v-progress-linear--reverse\n  .v-progress-linear__indeterminate\n    .long, .short\n      left: auto\n      right: 0\n\n    &--active .long\n      animation-name: indeterminate-rtl\n\n    &--active .short\n      animation-name: indeterminate-short-rtl\n\n.v-progress-linear__stream\n  animation: stream-ltr .25s infinite linear\n  border-color: currentColor\n  border-top: $progress-linear-stream-border-width dotted\n  bottom: 0\n  left: auto\n  right: -8px\n  opacity: $progress-linear-stream-opacity\n  pointer-events: none\n  position: absolute\n  top: calc(50% - #{$progress-linear-stream-border-width / 2})\n  transition: inherit\n\n.v-progress-linear--reverse\n  .v-progress-linear__stream\n    animation: stream-rtl .25