keyframes query-rtl\n  0%\n    right: 100%\n    left: -90%\n  60%\n    right: 100%\n    left: -90%\n  100%\n    right: -35%\n    left: 100%\n\n@keyframes query-short-ltr\n  0%\n    right: -200%\n    left: 100%\n  60%\n    right: 107%\n    left: -8%\n  100%\n    right: 107%\n    left: -8%\n\n@keyframes query-short-rtl\n  0%\n    right: 100%\n    left: -200%\n  60%\n    right: -8%\n    left: 107%\n  100%\n    right: -8%\n    left: 107%\n\n@keyframes stream-ltr\n  to\n    transform: translateX(-8px)\n\n@keyframes stream-rtl\n  to\n    transform: translateX(8px)\n","@import '../../styles/styles.sass';\n\n$progress-linear-border-radius: $border-radius-root !default;\n$progress-linear-stream-opacity: 0.3 !default;\n$progress-linear-stream-border-width: 4px !default;\n$progress-linear-stripe-gradient: linear-gradient(\n  135deg,\n  hsla(0, 0%, 100%, .25) 25%,\n  transparent 0,\n  transparent 50%,\n  hsla(0, 0%, 100%, .25) 0,\n  hsla(0, 0%, 100%, .25) 75%,\n  transparent 0,\n  transparent\n) !default;\n$progress-linear-stripe-background-size: 40px 40px !default;\n"],"sourceRoot":""}]);
